.border {
  border: $padding-border solid $main-color-text;
  position: fixed;
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
  pointer-events: none;
}
.container {
  padding: $padding-border;
}
.etere-top-right {
  position: fixed;
  top: $padding-border * 3;
  right: $padding-border * 2;
  padding: 20px;
}

.etere-top-left {
  position: fixed;
  top: $padding-border * 3;
  left: $padding-border * 2;
}
.etere-bottom-right {
  position: fixed;
  bottom: $padding-border * 3;
  right: $padding-border * 2;
}
.etere-bottom-left {
  position: fixed;
  bottom: $padding-border * 3;
  left: $padding-border * 2;
}
.main-content {
  margin: 80px $padding-border 0 $padding-border;
  text-align: center;
}

.icon-layout {
  width: 6rem;
  height: 6rem;
}

/* Product page */

.product {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  align-items: flex-start;

  @media only screen and (max-width: 57em) {
    flex-direction: column;
    align-items: center;
  }
  &-cover {
    flex-basis: 15%;
  }
  &-header {
    font-size: 4rem;
    text-transform: uppercase;
    color: $main-color-links;
  }
  &-info {
    flex-basis: 15%;
    text-align: left;
    font-size: 1.1rem;
  }
  &-image {
    & > * > img {
      width: 100%;
    }
    &-1 {
      margin-top: 25rem;
      flex-basis: 35%;
    }
    &-2 {
      margin-top: 10rem;
      flex-basis: 30%;
    }
    &-3 {
      margin-top: -30rem;
      flex-basis: 25%;
      @media only screen and (max-width: 112.5em) {
        margin: 0;
      }
    }
    &-4 {
      flex-basis: 15%;
    }
    &-5 {
      margin-top: 5rem;
      flex-basis: 15%;
    }
    &-6 {
      margin-top: 15rem;
      flex-basis: 15%;
    }
  }
  &-box {
    transition: transform 0.2s;
    @media only screen and (max-width: 37.5em) {
      flex-basis: auto;
      margin: 0;
    }
    & > img {
      width: 100%;
    }
  }
}

.collection {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &:not(:first) {
    margin-top: 25rem;
  }

  &-product {
    flex-basis: 30%;
    height: 60rem;
    display: flex;

    &-odd {
      height: auto;
      align-self: flex-end;
    }

    &-flex-text {
      text-transform: uppercase;
      text-align: left;
    }
  }

  &-name {
    color: $main-color-links;
    font-size: 7rem;
    margin-top: 12rem;
    margin-left: 60%;
    text-decoration: underline;
    text-transform: uppercase;
    width: 15%;
    word-wrap: anywhere;
    text-align: right;

    &::before {
      content: "/";
    }
    &::after {
      content: " collection";
    }
  }
}
.product-price-and-shipping {
  color: $main-color-links;
  font-weight: 700;
}



