.customer {
  display: inline-flex;
  align-items: center;

  &-logged {
    border-bottom: 1rem solid $main-color-links;
  }
  &-username {
    text-transform: uppercase;
    color: $main-color-links;
  }
}

.menu {
  color: $main-color-links;
  text-align: right;
  @include clearfix;

  &-buylink {
    display: inline-block;
    font-size: 2rem;
    width: 8rem;
    text-transform: uppercase;
    text-align: center;
    & > a {
      margin-left: 2rem;
    }
  }
  &-icon {
    display: inline-block;
    width: 3.5rem;
    transition: all 0.2s;

    &-rotated {
      transform: rotate(-90deg);
    }
  }

  &-list {
    text-transform: uppercase;
    text-align: right;
    font-size: 2rem;
    margin-top: 2rem;
    margin-right: 9rem;
  }
  &-item {
    display: none;
    list-style-type: none;
    &-current {
      display: inline-block;
      border-bottom: 0.3rem solid $main-color-links;
    }
  }
}

.blockcart-wrapper {
  display: inline-flex;
  align-items: center;
}

.product {
  &-price {
    color: $main-color-links;
    text-align: right;
    font-weight: 700;
    font-size: 1.5rem;
  }
  &-actions {
    text-align: center;
  }
  &-variants{
    text-align: left;
    margin: 2rem 0;
    & > label{
      font-weight: 700;
      margin-bottom: 1rem;
    }
  }
}

.add-to-cart {
  border: none;
  background-color: yellow;
  padding: 6px 12px 6px 12px;
  color: $main-color-links;
  border-bottom: 3px solid;
  font-weight: 900;
  cursor: pointer;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.group-container{
  margin-top:1rem;
}

.input-container{
  display: inline;
  
  margin: 0 .5rem;
  &>label{
    cursor: pointer;
  }
  &>input{
    display: none;
  }
  &-selected{
    border-bottom: 3px solid black
  }
}


